import axios from 'axios';

// Develop server URL
var postBaseUrl = 'http://localhost:8080/api';

//const postBaseUrl = 'http://labtalk.ap-northeast-1.elasticbeanstalk.com/api';

export function newActivity(newtitle, newtime, newdata, group_id, day) {
    var url = postBaseUrl + '/createactivity';

    console.log('Making POST request to: ' + url);

    return axios.post(url, {
        newtitle: newtitle,
        newtime: newtime,
        newdata: newdata,
        group_id: group_id,
        day: day
    }).then(function (res) {
        if (res.status !== 200) throw new Error('Unexpected response code: ' + res.status);

        return res.status;
    });
}

export function select_group_activity(group_id) {
    var url = postBaseUrl + '/selectactivity';

    console.log('Making POST request to: ' + url);

    return axios.post(url, {
        group_id: group_id
    }).then(function (res) {
        if (res.status !== 200) throw new Error('Unexpected response code: ' + res.status);

        return res.data;
    });
}


